:root {
  --font-body: Arial, Helvetica, sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --red: hsl(0, 100%, 50%);
  --gray: hsl(0, 0%, 20%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  color: var(--white);
  background-color: var(--black);
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active,
select {
  text-decoration: none;
  color: var(--white);
}

input,
button {
  border-width: 0;
  padding: 1.25rem 0;
  font-size: 1.5625rem;
}

.wrapper {
  background: url("https://assets.nflxext.com/ffe/siteui/vlv3/68c9706b-acd1-4472-bb1d-ef3ca933154c/298a5fdb-322c-44cb-839c-0b144a4b7159/US-en-20211011-popsignuptwoweeks-perspective_alpha_website_large.jpg") no-repeat 50%/cover;
  border-bottom: 0.46875rem solid var(--gray);
  min-height: 46.875rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.wrapper::before {
  content: "";
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
}
.wrapper * {
  z-index: 1;
}

.container {
  padding: 0 1.25rem;
  max-width: 46.25rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 1.5625rem 3.125rem;
}
.header__logo {
  max-width: 12.5rem;
  height: 3.125rem;
  fill: var(--red);
}
.header__select, .header__login {
  border-radius: 0.15625rem;
  font-weight: 700;
}
.header__select {
  border: 0.09375rem solid var(--white);
  background-color: transparent;
  padding: 0.46875rem 0.78125rem;
}
.header__select option {
  background-color: var(--gray);
}
.header__login {
  background-color: var(--red);
  padding: 0.53125rem 1.09375rem;
  margin-left: 1.5625rem;
}

.hero {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.hero__title {
  font-size: 4.0625rem;
}
.hero__subtitle {
  font-size: 1.5625rem;
}
.hero__paragraph {
  font-size: 1.125rem;
}
.hero__form {
  padding: 0 1.15625rem;
}
.hero__form input {
  min-width: 31.25rem;
  padding-left: 0.46875rem;
}
.hero__form button {
  padding-right: 3.125rem;
  padding-left: 1.25rem;
  background-color: var(--red);
  color: var(--white);
}